import { Component, HostListener, Input, ViewEncapsulation } from '@angular/core';
import {  RouterModule } from '@angular/router';
import { MatIconModule } from '@angular/material/icon';
import { NgForOf, NgIf } from '@angular/common';
import { DscBreadcrumbItem } from './shared/dsc-breadcrumb-item';


@Component({
  selector: 'dsc-breadcrumb',
  templateUrl: './dsc-breadcrumb.component.html',
  styleUrls: ['./dsc-breadcrumb.component.scss'],
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [MatIconModule, RouterModule, NgIf, NgForOf]
})
export class DscBreadcrumbComponent {

  isScreenSmall: boolean = false;

  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    this.checkScreenSize();
  }

  @Input()
  urls?: DscBreadcrumbItem[] = [];

  @Input() homeUrl: string = '/'

  truncatedUrls: DscBreadcrumbItem[] = [];

  showFullBreadcrumb = false;

  breadcrumbAriaLabel = 'Expandir links, recolhido';

  ngOnInit() {
    this.updateTruncatedUrls();
    this.checkScreenSize();
  }

  checkScreenSize() {
    this.isScreenSmall = window.innerWidth < 600;
  }

  updateTruncatedUrls() {
    if (this.urls && this.urls.length > 4) {
      this.truncatedUrls = [
        ...this.urls.slice(0, 1),
        { label: '...', url: null },
        ...this.urls.slice(-2)
      ];
    } else {
      this.truncatedUrls = this.urls || [];
    }
  }

  handleKeydown(event: KeyboardEvent) {
    if (event.key === 'Enter' || event.key === ' ') {
      event.preventDefault();
      this.toggleBreadcrumb();
    }
  }

  toggleBreadcrumb() {
    if (!this.showFullBreadcrumb) {
      this.breadcrumbAriaLabel = 'Expandido';
  
      setTimeout(() => {
        this.showFullBreadcrumb = true;
      }, 500);
    } else {
      this.showFullBreadcrumb = false;
      this.breadcrumbAriaLabel = 'Expandir links, recolhido';
    }
  }

  get displayedUrls() {
    return this.showFullBreadcrumb ? this.urls : this.truncatedUrls;
  }

}
